import React, { useContext, useEffect, useState } from "react";
import { useFormik } from "formik";
import * as yup from "yup";
import { RiFilter2Line } from "react-icons/ri";
import axios from "axios";
import { useNavigate, useLocation } from "react-router-dom";
import BarLoader from "../../../Components/Common/BarLoader";
import ApiHeader from "../../../Components/ApiList/ApiHeader";
import { contextVar } from "../../../Components/ContextVar";
import { allowCharacterNumberSlashInput, nullToNA } from "../../../Components/Common/PowerUps/PowerupFunctions";
import PropertyApiList from "../../../Components/ApiList/PropertyApiList";
import OtpCard from "./OtpCard";
import CitizenApplyApiList from "../../../Components/CitizenApplyApiList";
import useSetTitle from "../../../Components/GlobalData/UseSetTitle";
import BrandLoader from "../../../Components/Common/BrandLoader";
import CommonModal from "../../../Components/GlobalData/CommonModal";
import ServerErrorCard from "../../AdvertisementModule/ServerErrorCard";
import { apiTimeout } from "../../../Components/ApiList/Constants";
import BottomErrorCard from "../../../Components/BottomErrorCard";
import MobileNoCard from "./MobileNoCard";
import AxiosInterceptors from "../../../Components/GlobalData/AxiosInterceptors";


function SearchHolding() {
    const [isLoading, setisLoading] = useState(true);
    const [readymadeListData, setreadymadeListData] = useState();
    const [readymadeListStatus, setreadymadeListStatus] = useState(false);
    const [selectedPropId, setselectedPropId] = useState(null);
    const [selectedUlbId, setselectedUlbId] = useState(null);
    const [dataInfo, setdataInfo] = useState('');
    const [ulbList, setulbList] = useState();
    const { notify } = useContext(contextVar)
    const [otpCardStatus, setotpCardStatus] = useState(false)
    const [verifedMobileNo, setverifedMobileNo] = useState(null)
    const [erroState, seterroState] = useState(false);
    const [erroMessage, seterroMessage] = useState(null);


    useSetTitle('Search Property')
    const { api_searchDirectHolding } = PropertyApiList();
    const { api_getAllUlb, api_sendMobileOtp } = CitizenApplyApiList()

    const navigate = useNavigate();

    // FUNCTION TO GET PROPERTY DETAILS
    const fetchData = (data) => {
        seterroState(false)
        setdataInfo('')
        setreadymadeListStatus(false)
        setisLoading(true)
        const requestBody = {
            holdingNo: data?.holdingNo,
            ulbId: data?.ulbId,
        };
        console.log('before fetch holding details....', requestBody)
        axios
            .post(api_searchDirectHolding, requestBody)
            .then((res) => {
                console.log('search property list', res?.data)
                if (res?.data?.status) {
                    setreadymadeListData(res?.data?.data)
                    setreadymadeListStatus(true)
                    setdataInfo('')
                    setselectedUlbId(requestBody?.ulbId)
                    // TO CHANGE THE URL TO REFETCH DATA WHEN COME BACK
                    // changeUrl(`/admin/search/fresh/${encodeURIComponent(data?.filterBy)}/${encodeURIComponent(data?.parameter)}`)
                } else {
                    notify('This holding no does not exits!', 'error')
                    setdataInfo('Data not found. Please enter a valid holding no. or related ULB')
                }
                setisLoading(false)
            })
            .catch((err) => {
                console.log("Error while fetching Filter Data", err)
                notify('Something went wrong!!', 'error')
                setdataInfo('Network problem.')
                activateBottomErrorCard(true, 'Error occured. Please try again later.')
                setisLoading(false)
                setreadymadeListStatus(false)
            });
    };


    const validationSchema = yup.object({
        holdingNo: yup.string().required("This is a required field !"),
        ulbId: yup.string().required("Select ULB"),
    });

    const formik = useFormik({
        initialValues: {
            holdingNo: "",
            ulbId: "",
        },
        onSubmit: (values) => {
            console.log('at submit form.....', values)
            fetchData(values);
        },
        validationSchema,
    });



    const handleChange = (e) => {
        let name = e.target.name
        let value = e.target.value

        { name == 'holdingNo' && formik.setFieldValue("holdingNo", allowCharacterNumberSlashInput(value, formik.values.holdingNo, 16)) }
    };

    //  FUNCTION WHICH SENDS OTP TO REGISTERED MOBILE NO
    const sendOtp = (passedMobileNo = null) => {
        console.log('passed mobile no.....')
        setisLoading(true)
        let requestBody
        if (passedMobileNo == null) {
            requestBody = {
                mobileNo: verifedMobileNo
            }
        } else {
            requestBody = {
                mobileNo: passedMobileNo
            }
        }

        console.log('before send otp. of direct search..', requestBody)
        axios
            .post(api_sendMobileOtp, requestBody)
            .then((response) => {
                console.log('otp send response', response?.data)
                if (response?.data?.status) {
                    setotpCardStatus(true)
                    notify('OTP is send to your mobile no.', 'success')
                } else {
                    notify(response?.data?.message, 'error')
                    activateBottomErrorCard(true, 'Error occured. Please try again later.')
                }
                setisLoading(false)

            })
            .catch((err) => {
                console.log("error otp send", err)
                activateBottomErrorCard(true, 'Error occured. Please try again later.')
                setisLoading(false)
            });
    }

    // FUNCTION TO FETCH ULB LIST
    const fetchULBList = () => {
        seterroState(false)
        setisLoading(true)
        AxiosInterceptors.get(api_getAllUlb, { timeout: apiTimeout })
            .then(function (response) {
                console.log('ulb list. at..', response?.data)
                if (response?.data?.status) {
                    setulbList(response?.data?.data)
                } else {
                    seterroState(true)
                }
                setisLoading(false)
            })
            .catch(function (error) {
                console.log('ulb list error.... ', error);
                seterroState(true)
                setisLoading(false)
            })
    }

    useEffect(() => {
        fetchULBList()
    }, []);

    const callback = () => {
        notify('Mobile no. has been verified successfully !', 'success')
        navigate(`/viewDemandHoldingPropertyDirect/${selectedPropId}`)
    }

    const closeOtpModal = () => {
        setotpCardStatus(false)
    }

    const activateBottomErrorCard = (state, msg) => {
        seterroMessage(msg)
        seterroState(state)

    }

    const actionFun = (id, mobile) => {
        setselectedPropId(id)

        if (mobile == '' || mobile == null || mobile == undefined) {
            return <MobileNoCard title="Update Mobile No." desc="Enter mobile no. to proceed for payment" setverifedMobileNo={(val) => {
                setverifedMobileNo(val)
                sendOtp(val)
            }} />
        } else {
            setverifedMobileNo(mobile)
            sendOtp(mobile)
        }

    }

    if (isLoading) {
        return (
            <>
                <BrandLoader />
                <div className='min-h-screen'></div>
            </>
        )
    }



    if (otpCardStatus) {
        return (
            <OtpCard closeOtpModal={closeOtpModal} bottomNaviation={true} headTitle="Pay Property Tax" reSendOtp={sendOtp} verifedMobileNo={verifedMobileNo} callback={callback} setotpCardStatus={setotpCardStatus} />
        )
    }

    if (!otpCardStatus) {
        return (


            <>

                {
                    isLoading && <BarLoader />
                }
                {erroState && <BottomErrorCard activateBottomErrorCard={activateBottomErrorCard} errorTitle={erroMessage} />}


                <div className="px-2 md:px-10 mt-16">
                    <div className="border shadow-xl bg-white mt-6">
                        <div className="flex ml-5 mt-2 ">
                            <p className="font-semibold text-3xl ml-4 mt-1 text-gray-600">
                                Search Property
                            </p>
                        </div>
                        <form onSubmit={formik.handleSubmit} onChange={handleChange}>
                            <div className="flex flex-col md:flex-row justify-center my-5 m-10 ">

                                <div className={`form-group col-span-12 mb-4 md:px-10`}>
                                    <label className={`form-label inline-block mb-1 text-gray-600 text-sm font-semibold`}>ULB<small className="mt-1 text-sm font-semibold text-red-600 inline ">*</small></label>
                                    <select id='basic_details_1' {...formik.getFieldProps('ulbId')} className={`form-control block w-full px-3 py-1.5 text-base font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none shadow-md cursor-pointer `}>
                                        <option value="" disabled selected>Select</option>
                                        {
                                            ulbList?.map((data) => (
                                                <option value={data.id}>{data.ulb_name}</option>
                                            ))
                                        }
                                    </select>
                                    <span className="text-red-600 absolute text-xs">{formik.touched.ulbId && formik.errors.ulbId ? formik.errors.ulbId : null}</span>
                                </div>

                                <div>
                                    <label className="form-label inline-block mb-1 text-gray-600 text-sm font-semibold">
                                        Holding No.<span className="text-red-500">*</span>
                                    </label>
                                    <input
                                        type="text"
                                        {...formik.getFieldProps('holdingNo')}
                                        className=" w-full px-3 py-1.5 text-base font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none placeholder-gray-300 shadow-md"
                                    />
                                    <p className="text-red-500 text-xs">
                                        {formik.touched.holdingNo && formik.errors.holdingNo
                                            ? formik.errors.holdingNo
                                            : null}
                                    </p>
                                </div>
                                <div className="mt-4 md:px-10">
                                    <button
                                        type="submit"
                                        className="w-full border border-indigo-500 bg-indigo-600 hover:bg-indigo-500 text-white hover:text-black shadow-lg rounded-sm text-base font-semibold px-5 m-3 py-1"
                                    >
                                        <p className="flex text-center justify-center items-center">
                                            <RiFilter2Line className="inline mr-2" />
                                            Search record
                                        </p>
                                    </button>
                                </div>
                            </div>
                        </form>
                        {/* View Search Result in List Table */}

                    </div>
                    <div className="mt-10">
                        {readymadeListStatus && readymadeListData?.length != 0 &&
                            // <ListTable columns={COLUMNS} dataList={readymadeListData} />
                            <div className='col-span-10 bg-gray-50 p-4 overflow-x-scroll'>
                                <div className='flex flex-row'>
                                    <h1 className='px-1 font-semibold font-serif text-2xl mt-4'>All Transcations</h1>
                                </div>

                                <table className='min-w-full leading-normal mt-10'>
                                    <thead className='font-bold text-left text-sm bg-indigo-50 text-gray-600'>
                                        <tr>
                                            <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">#</th>
                                            <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">ACTION</th>
                                            <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">OWNER'S NAME</th>
                                            <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">HOLDING NO</th>
                                            <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">NEW HOLDING NO</th>
                                            <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">MOBILE NO</th>
                                            <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">WARD NO.</th>
                                            <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">ULB</th>
                                            <th className="px-2 py-3 border-b border-gray-200  text-xs uppercase text-left">ADDRESS</th>
                                        </tr>
                                    </thead>
                                    <tbody className="text-sm">

                                        <>
                                            {readymadeListData?.map((data) => (
                                                <tr className="bg-white shadow-lg border-b border-gray-200">
                                                    <td className="px-2 py-2 text-sm text-left">1</td>
                                                    <td className="px-2 py-2 text-sm text-left"> <button
                                                        onClick={() => actionFun(data?.id, data?.mobile_no)}
                                                        className="bg-sky-200 px-3 py-1 rounded-lg shadow-lg hover:shadow-xl hover:bg-sky-800 hover:text-white text-black"
                                                    >
                                                        View
                                                    </button></td>
                                                    <td className="px-2 py-2 text-sm text-left">{nullToNA(data?.owner_name)}</td>
                                                    <td className="px-2 py-2 text-sm text-left">{nullToNA(data?.holding_no)}</td>
                                                    <td className="px-2 py-2 text-sm text-left">{nullToNA(data?.new_holding_no)}</td>
                                                    <td className="px-2 py-2 text-sm text-left">{nullToNA(data?.mobile_no)}</td>
                                                    <td className="px-2 py-2 text-sm text-left">{nullToNA(data?.ward_name)}</td>
                                                    <td className="px-2 py-2 text-sm text-left">{nullToNA(data?.ulb)}</td>
                                                    <td className="px-2 py-2 text-sm text-left">{nullToNA(data?.prop_address)}</td>

                                                </tr>
                                            ))}

                                        </>

                                    </tbody>
                                </table>

                            </div>
                        }

                        <div className="text-xl font-semibold text-red-400 text-center">{dataInfo}</div>
                        <div className="w-full h-40"></div>
                    </div>
                </div>

            </>
        )
    }
}

export default SearchHolding;
